}
#app .slide-left-enter, #app .slide-left-leave-to {
    -webkit-transform: translateX(calc(50vw + 50%));
            transform: translateX(calc(50vw + 50%));
}
#app .switch-card-enter-active, #app .switch-card-leave-active {
    -webkit-transition: all 0.8s cubic-bezier(0.75, -0.5, 0, 1.75);
    transition: all 0.8s cubic-bezier(0.75, -0.5, 0, 1.75);
}
#app .switch-card-enter {
    -webkit-transform: translateX(calc(50vw + 50%)) translateY(-50%);
            transform: translateX(calc(50vw + 50%)) translateY(-50%);
}
#app .switch-card-leave-to {
    -webkit-transform: translateX(calc(-50vw - 105%)) translateY(-50%);
